import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { AiOutlineGoogle } from 'react-icons/ai';
import { useSearchParam } from 'react-use';

import { SocialLoginButton } from './SocialLoginButton';

export const GoogleLoginButton = ({ beforeProvider, ...rest }) => {
    const { t } = useTranslation('auth');
    const redirectURI = useSearchParam('redirect_uri');
    const loginUrl = `/api/auth/login/google${redirectURI ? `?redirect_uri=${redirectURI}` : ''}`;

    return (
        <SocialLoginButton icon={AiOutlineGoogle} as="a" href={loginUrl} {...rest}>
            {t(beforeProvider || 'auth.loginWith', { provider: 'Google' })}
        </SocialLoginButton>
    );
};

GoogleLoginButton.propTypes = {
    beforeProvider: PropTypes.string,
};
