import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { FaApple } from 'react-icons/fa';

import { SocialLoginButton } from './SocialLoginButton';
import { useSearchParam } from 'react-use';

export const AppleLoginButton = ({ beforeProvider, ...rest }) => {
    const { t } = useTranslation('auth');
    const redirectURI = useSearchParam('redirect_uri');
    const loginUrl = `/api/auth/login/apple${redirectURI ? `?redirect_uri=${redirectURI}` : ''}`;

    return (
        <SocialLoginButton icon={FaApple} as="a" href={loginUrl} backgroundColor="#000" {...rest}>
            {t(beforeProvider || 'auth.loginWith', { provider: 'Apple' })}
        </SocialLoginButton>
    );
};

AppleLoginButton.propTypes = {
    beforeProvider: PropTypes.string,
};
